.bmi-container{
width:100%;
}

.title{
    color:purple;
    text-align: center;
    padding: 2%;
    background-color: gainsboro;
    font-size: xx-large;
    
}

td{
    box-sizing: border-box;
    width:20%;
    padding:2%;
}

input{box-sizing: border-box;}

.table{
    /* border-spacing: 20px; */
    /* border:1px solid; */
    width:80%;
    margin:auto;

    
}

select{width:100%;
height:100%;
border:0}


.buttons{
    display:flex;
    justify-content: space-around;
    margin:2% 0;
    border-radius:15px;


}

button:active{
    transform:scale(0.95,0.95)
}

.calculate,.clear{
    width:20%;
    height:50px;
}

button{
    width:100%;
    height:100%;
    color:white;
    font-size: larger;
    border-radius: 10px;
}



